<script setup lang="ts">
import { type ButtonHTMLAttributes, type Component, computed, toRefs } from 'vue'
import { RouterLink } from 'vue-router/auto'

import { type AsTag, type ClassValue, type VariantProps, cn } from '@/shared/lib/style'
import type { RouteTo } from '@/shared/lib/router'

import type { Size, Variant } from './types'
import { button } from './lib'

type ButtonProps = VariantProps<typeof button>

export interface Props extends /* @vue-ignore */ ButtonHTMLAttributes {
  class?: ClassValue
  as?: Component | AsTag
  to?: RouteTo
  type?: HTMLButtonElement['type']
  variant?: Variant
  size?: Size
  rounded?: boolean
  icon?: boolean
}

const props = withDefaults(defineProps<Props>(), {
  as: 'button',
  type: 'button',
  variant: 'primary',
  size: 'base',
})

export interface Slots {
  default: any
  prepend: any
  append: any
}

const slots = defineSlots<Slots>()

const { icon, to, as } = toRefs(props)

const asComputed = computed(() => {
  if (to.value) {
    return RouterLink
  }

  return as.value
})

const _content = computed((): ButtonProps['_content'] => {
  if (slots.prepend || slots.append) {
    return 'textAndIcon'
  }

  return icon.value ? 'icon' : 'text'
})
</script>

<template>
  <component
    :is="asComputed"
    :to="to"
    :class="cn(
      button({
        variant,
        size,
        rounded,
        _content,
      }),
      props.class ?? '',
    )"
  >
    <slot name="prepend" />

    <slot />

    <slot name="append" />
  </component>
</template>
